import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'
import { Helmet } from 'react-helmet'
import styled from 'styled-components'
import Img from 'gatsby-image'
import { FaTwitter, FaFacebookF, FaLinkedin } from 'react-icons/fa'

import Layout from '../components/layout'
import { MetaData } from '../components/common/meta'
import { NextPost } from '../components/common'

/**
 * Single post view (/:slug)
 *
 * This file renders a single post and loads all the content.
 *
 */

const StyledPost = styled.div`
  color: black;
  background: white;
  padding: 4rem 4rem 5rem;
  max-width: 1280px;
  margin: 0 auto;

  @media (max-width: 768px) {
    padding-right: 2rem;
    padding-left: 2rem;
    padding-bottom: 2rem;
  }

  header {
    text-align: center;
  }

  .post-full-content {
    padding: 0 5rem;

    @media (max-width: 768px) {
      padding: 3rem 1rem;
    }
    font-weight: 100;
    font-size: 14px;

    max-width: 1024px;
    margin: 0 auto;
  }

  img {
    max-width: 100%;
    width: 100%;
    height: auto;
  }
`

const StyledImg = styled(Img)`
  height: auto;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
`

const IconList = styled.ul`
  display: flex;
  flex-direction: row;
  margin-top: 2rem;

  & > *:not(:last-child) {
    padding-right: 1rem;
  }

  li {
    transition: opacity 0.3s ease-in-out;
    &:hover {
      opacity: 0.7;
    }

    a {
      color: black;
    }
  }
`

const Post = ({ data, location, pageContext }) => {
  const post = data.ghostPost
  const nextPost = data.nextPost

  return (
    <>
      <MetaData data={data} location={location} type="article" />
      <Helmet>
        <style type="text/css">{`${post.codeinjection_styles}`}</style>
      </Helmet>
      <Layout themeColor="white">
        <StyledPost className="container">
          <article className="content">
            <header>
              <h1 className="content-title">{post.title}</h1>
            </header>
            {post.feature_image ? (
              <figure className="post-feature-image">
                <StyledImg
                  fluid={post.localFeatureImage.childImageSharp.fluid}
                  alt={post.title}
                />
              </figure>
            ) : null}
            <section className="post-full-content">
              <section
                className="content-body load-external-scripts"
                dangerouslySetInnerHTML={{ __html: post.html }}
              />
              <div>
                <IconList>
                  <li>
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href={`http://www.twitter.com/share?url=${
                        typeof window !== 'undefined'
                          ? window.location.href
                          : ''
                      }`}
                    >
                      <FaTwitter />
                    </a>
                  </li>
                  <li>
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href={`http://www.facebook.com/share.php?u=${
                        typeof window !== 'undefined'
                          ? window.location.href
                          : ''
                      }`}
                    >
                      <FaFacebookF />
                    </a>
                  </li>
                  <li>
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href={`https://www.linkedin.com/sharing/share-offsite/?url=${
                        typeof window !== 'undefined'
                          ? window.location.href
                          : ''
                      }`}
                    >
                      <FaLinkedin />
                    </a>
                  </li>
                </IconList>
              </div>
            </section>
            {nextPost && <NextPost next={nextPost} />}
          </article>
        </StyledPost>
      </Layout>
    </>
  )
}

Post.propTypes = {
  data: PropTypes.shape({
    ghostPost: PropTypes.shape({
      codeinjection_styles: PropTypes.object,
      title: PropTypes.string.isRequired,
      html: PropTypes.string.isRequired,
      feature_image: PropTypes.string,
      localFeatureImageSharp: PropTypes.object,
    }).isRequired,
  }).isRequired,
  location: PropTypes.object.isRequired,
}

export default Post

export const postQuery = graphql`
  query($slug: String!, $next: String!) {
    ghostPost(slug: { eq: $slug }) {
      ...GhostPostFields
    }
    nextPost: ghostPost(slug: { eq: $next }) {
      ...GhostPostFields
    }
  }
`
